.reg-form__container {
  margin-left: 43.5rem;
  margin-top: 5rem;
}

.reg-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: center;
  background-color: #f8d04c;
  width: 25rem;
  border-radius: 0.6rem;
  padding: 4rem;
}

.reg-form__input {
  display: flex;
  flex-direction: column;
  text-align: left;
  gap: 0.5rem;
  margin-bottom: 1rem;
  font-size: 1.25rem;
}

.reg-form__input input {
  height: 1.65rem;
}

.button-submit {
  margin-top: 1.5rem;
}

/*=================================================*/
/*===================MEDIA-QUERIES=================*/
/*=================================================*/

/*------------*/
/*---TABLET---*/
/*------------*/

@media (max-width: 48rem) {
  .reg-form__container {
    margin-left: 7.5rem;
    margin-top: 2rem;
  }
}

/*----------------*/
/*---SMARTPHONE---*/
/*----------------*/

@media (max-width: 26rem) {
  .reg-form__container {
    margin-left: 0rem;
  }

  .reg-form {
    margin-top: 3rem;
    width: 100vw;
  }
}
