button {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  cursor: pointer;
  border: solid transparent;
  padding: 0.65rem;
  border-radius: 0.35rem;
  background-color: rgb(58, 189, 138);
  font-size: 1.15rem;
}

button:hover,
button:active {
  border: solid rgb(47, 145, 107);
  color: rgb(255, 255, 255);
}
